<template>
  <div id='container'>
    <van-nav-bar title="PK" left-arrow :border="true" @click-left="back()"></van-nav-bar>
    <div id="main" style="width:100vw;height:85vh"></div>

  </div>

</template>
<script>
  import echarts from 'echarts'
  // import '../../src/assets/china.js'
  const chinaJson = require("../../public/mapJson/china.json");
  var provinces = ['310000', '130000', '140000', '150000', '210000', '220000', '230000', '320000', '330000', '340000', '350000', '360000', '370000', '410000', '420000', '430000', '440000', '450000', '460000', '510000', '520000', '530000', '540000', '610000', '620000', '630000', '640000', '650000', '110000', '120000', '500000', '810000', '820000', '710000'];
  var provincesText = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];
  var provincesName = ['shanghai', 'hebei', 'shanxi', 'neimenggu', 'liaoning', 'jilin', 'heilongjiang', 'jiangsu', 'zhejiang', 'anhui', 'fujian', 'jiangxi', 'shandong', 'henan', 'hubei', 'hunan', 'guangdong', 'guangxi', 'hainan', 'sichuan', 'guizhou', 'yunnan', 'xizang', 'shanxi1', 'gansu', 'qinghai', 'ningxia', 'xinjiang', 'beijing', 'tianjin', 'chongqing', 'xianggang', 'aomen', 'taiwan'];
  export default {
    data() {
      return {
        myChart: '',
        distributionOptions: '',
        locatePoints: []
      }

    },
    created() {



    },
    async mounted() {
      const { data: res } = await this.$http.get('/user/queryNearbyPerson')

      var arr = res.content
      for (let i = 0; i < arr.length; i++) {
        this.locatePoints[i] = {
          name: '' + i,
          value: [arr[i].x, arr[i].y],
          symbol: 'pin',
          label: {
            position: 'bottom',
            // formatter: (i) => {
            //   return '' + i
            // }
          }
        }
      }
      this.init();
    },
    methods: {
      randomValue() {
        return Math.floor(Math.random() * 100)
      },

      // 
      init() {

        echarts.registerMap("china", chinaJson);
        this.changeOptions("china")
        this.myChart = echarts.init(document.getElementById("main"));
        this.myChart.setOption(this.distributionOptions);
        // json
        this.myChart.on("click", chinaParam => {
          let code = provinces[provincesText.indexOf(chinaParam.name)] || 100000
          this.getProvinceMapOpt(code, provincesName[provincesText.indexOf(chinaParam.name)])
        });
        window.onresize = function () {
          this.myChart.resize();
        };
      },
      changeOptions(name) {

        // 
        const seriesList = [
        {
          data: this.locatePoints
        },
        ];
        // 
        const series = seriesList.map(v => {
          return {
            type: "scatter", //
            coordinateSystem: "geo",
            data: v.data
          };
        });

        // options
        this.distributionOptions = {
          tooltip: { // 
            show: true, // 
            trigger: "item", // 
            triggerOn: "mousemove", // 
            formatter: ":{b}<br/>:{c}"
          },
          series, // 
          geo: {
            map: name || 'china', //   
            layoutCenter: ["50%", "50%"], //left/right/top/bottom
            layoutSize: "55%",
            roam: true, //
            zoom: 2,
            label: {
              normal: {
                //
                show: true, //
                textStyle: {
                  color: "#3d3a37",
                  fontSize: 10,
                  fontFamily: "Arial",
                }
              },
              emphasis: { // 
                //
                color: "#fff"
              }
            },
            itemStyle: { //  
              normal: {
                borderColor: "#07919e", // 
                // areaColor: "#1c2f59", //  
                areaColor: "white", //  
                textStyle: { // 
                  color: "#fff"
                },
                shadowBlur: 8, // 
                shadowOffsetX: 2 // 
              },
              emphasis: {
                areaColor: "#1c2f59",
                color: "#fff"
              }
            }
          }
        };
      },

      //
      getProvinceMapOpt(provinceAlphabet, name) {
        var path = '/mapJson/province/' + provinceAlphabet + '.json'
        if (provinceAlphabet === 100000) {
          path = '/mapJson/china.json'
        }
        axios.get(path).then((s) => {
          echarts.registerMap(name, s.data)
          this.changeOptions(name)
          this.myChart.setOption(this.distributionOptions, true);
        })
      },


      // drawChart() {
      // var myChart = echarts.init(document.getElementById('main'));

      // let option = {
      //   tooltip: {
      //     formatter: function (params, ticket, callback) {
      //       return (
      //         params.seriesName + "<br />" + params.name + "" + params.value
      //       );
      //     }, //
      //   },
      //   visualMap: {
      //     min: 0,
      //     max: 200,
      //     left: "left",
      //     top: "bottom",
      //     text: ["", ""], //
      //     inRange: {
      //       color: ["#FFF", "tomato"], //
      //     },
      //     show: true, //
      //   },
      //   geo: {
      //     map: "china",
      //     roam: false, //
      //     zoom: 1.23, //
      //     label: {
      //       normal: {
      //         show: true,
      //         fontSize: "10",
      //         color: "rgba(0,0,0,0.7)",
      //       },
      //     },
      //     itemStyle: {
      //       normal: {
      //         borderColor: "rgba(0, 0, 0, 0.2)",
      //       },
      //       emphasis: {
      //         areaColor: "#F3B329", //
      //         shadowOffsetX: 0,
      //         shadowOffsetY: 0,
      //         shadowBlur: 20,
      //         borderWidth: 0,
      //         shadowColor: "rgba(0, 0, 0, 0.5)",
      //       },
      //     },
      //   },
      //   series: [
      //   {
      //     name: "",
      //     type: "map",
      //     geoIndex: 0,
      //     data: this.dataList,
      //   }, ],

      // };
      // myChart.setOption(option);
      // }
    }
  }
</script>
